import React, { useEffect, useRef, useState } from "react";
import { Route, Routes, useLocation } from "react-router-dom";
import { connect, Connect, useDispatch, useSelector } from "react-redux";
import { createBucket, isLoaded, loadBucket } from "./redux/modules/bucket";
import { addDoc, collection, getDocs } from "firebase/firestore";
import { db } from "./firebase";

// import [ ] from [  ];
import BucketList from "./BucketList";
import styled from "styled-components";
import Detail from "./Detail";
import NotFound from "./NotFound";
import Progress from "./Progress";
import Spinner from "./Spinner";

//      props   .
const mapStateTopProps = (state) => ({
    bucket_list: state.bucket.list,
});

//         props   .
const mapDispatchToProps = (dispatch) => ({
    load: () => {
        dispatch(loadBucket());
    },
    create: (new_item) => {
        console.log(new_item);
        dispatch(createBucket(new_item));
    },
});

//    !
const App = () => {
    const dispatch = useDispatch();
    //const bucket_list = useSelector((state) => state.bucket.list);
    let is_Loaded = useSelector((state) => state.bucket.is_Loaded);

    useEffect(() => {
        let bucket_data = [];
        const querySnapshot = getDocs(collection(db, "bucket_list"));

        querySnapshot.then(function (data) {
            data.forEach((doc) => {
                if (doc.exists) {
                    bucket_data = [...bucket_data, { id: doc.id, ...doc.data() }];
                }
            });
            dispatch(loadBucket(bucket_data));
            dispatch(isLoaded(true));
        });

        // []   componentDidMount, componentDidUpdate  (  )
        // []    function componentDidMount   (componentDidMount   )

        return () => {
            console.log("  ");
        };
    }, []);

    return (
        <div className="App">
            <Container>
                <Title> </Title>
                <Progress />
                <Line />
                {/*  . */}
                {/* <  [props ]={ (, , , ...)}/> */}
                {/* Route   2  ! */}
                {!is_Loaded ? (
                    <Spinner />
                ) : (
                    <Routes>
                        <Route path="/" element={<BucketList />} />
                        <Route path="/detail" element={<Detail />}>
                            <Route path=":id" element={<Detail />} />
                        </Route>
                        <Route path={"*"} element={<NotFound />} />
                    </Routes>
                )}
            </Container>
        </div>
    );
};

const Input = styled.div`
    max-width: 350px;
    min-height: 10vh;
    background-color: #fff;
    padding: 16px;
    margin: 20px auto;
    border-radius: 5px;
    border: 1px solid #ddd;
`;

const Container = styled.div`
    max-width: 350px;
    min-height: 60vh;
    background-color: #fff;
    padding: 16px;
    margin: 20px auto;
    border-radius: 5px;
    border: 1px solid #ddd;
`;

const Title = styled.h1`
    color: slateblue;
    text-align: center;
`;

const Line = styled.hr`
    margin: 16px 0px;
    border: 1px dotted #ddd;
`;

export default connect(mapStateTopProps, mapDispatchToProps)(App);
